import { union, type Type } from 'io-ts';
import {
  Ed25519SigningAlgorithm,
  type Ed25519SignMessageSchema,
} from './algorithms/Ed25519SigningAlgorithm';
import {
  BIP340SigningAlgorithm,
  type BIP340SignMessageSchema,
} from './algorithms/BIP340SigningAlgorithm';
import {
  EcdsaSigningAlgorithm,
  type EcdsaSignMessageSchema,
} from './algorithms/EcdsaSigningAlgorithm';
import type { ISigningAlgorithm } from './base';

/**
 * üéØ SINGLE SOURCE OF TRUTH - Add new algorithms here and everything else is automatic!
 * This is the ONLY place you need to add new signing algorithms
 */
export const SIGNING_ALGORITHM_CLASSES = {
  ed25519: Ed25519SigningAlgorithm,
  bip340: BIP340SigningAlgorithm,
  ecdsa: EcdsaSigningAlgorithm,
} as const;

/**
 * üìù Explicit union type of all signing algorithm schemas
 * (Only other place you need to add new algorithms)
 */
export type AllSigningAlgorithmSchemas =
  | Ed25519SignMessageSchema
  | BIP340SignMessageSchema
  | EcdsaSignMessageSchema;

// ========================================
// ü§ñ EVERYTHING BELOW IS AUTOMATICALLY DERIVED
// ========================================

/**
 * Derive signing algorithm name type from classes
 */
export type SigningAlgorithmName = keyof typeof SIGNING_ALGORITHM_CLASSES;

/**
 * Derive runtime array of algorithm names from classes
 */
export const ALL_SIGNING_ALGORITHM_NAMES = Object.keys(
  SIGNING_ALGORITHM_CLASSES
) as SigningAlgorithmName[];

/**
 * Create algorithm instances (derived from classes)
 */
export const SIGNING_ALGORITHM_INSTANCES: Record<
  SigningAlgorithmName,
  ISigningAlgorithm
> = {
  ed25519: new SIGNING_ALGORITHM_CLASSES.ed25519(),
  bip340: new SIGNING_ALGORITHM_CLASSES.bip340(),
  ecdsa: new SIGNING_ALGORITHM_CLASSES.ecdsa(),
};

/**
 * Create union schema (derived from instances)
 */
export const ALL_SIGNING_ALGORITHM_SCHEMA: Type<AllSigningAlgorithmSchemas> =
  (() => {
    const schemas = Object.values(SIGNING_ALGORITHM_INSTANCES).map(
      (instance) => instance.schema
    );

    if (schemas.length === 0) {
      throw new Error('No signing algorithms defined');
    }

    if (schemas.length === 1) {
      return schemas[0] as Type<AllSigningAlgorithmSchemas>;
    }

    return union([
      schemas[0],
      schemas[1],
      ...schemas.slice(2),
    ]) as Type<AllSigningAlgorithmSchemas>;
  })();

/**
 * Helper type to extract algorithm-specific schema
 */
export type SigningAlgorithmSchemaFor<
  T extends AllSigningAlgorithmSchemas['signingAlgo'],
> = Extract<AllSigningAlgorithmSchemas, { signingAlgo: T }>;

/**
 * Utility to check if a string is a valid signing algorithm name
 */
export function isValidSigningAlgorithm(
  name: string
): name is SigningAlgorithmName {
  return name in SIGNING_ALGORITHM_CLASSES;
}

/**
 * Create keygen result from secret share using algorithm-specific logic
 */
export function createKeygenResultFromSecretShare(
  algorithmName: SigningAlgorithmName,
  secretShare: string
): any {
  const algorithm = SIGNING_ALGORITHM_INSTANCES[algorithmName];
  if (!algorithm) {
    throw new Error(`Unknown signing algorithm: ${algorithmName}`);
  }
  return algorithm.createKeygenResultFromSecretShare(secretShare);
}
