import { TypeOf } from 'io-ts';
import {
  Uint8ArrayCodec,
  type TraceContext,
  TraceContextCodec,
} from '../codecs';
import {
  buildMessageSchema,
  createStandardMessage,
  createStandardDecoder,
} from './utils';

/**
 * Handshake Request Runtime Data
 */
export interface HandshakeRequestData {
  challenge: Uint8Array;
  nonce: Uint8Array;
  traceContext?: TraceContext;
}

/**
 * Handshake Request Wire Format
 */
export interface HandshakeRequestWire {
  type: 'handshake';
  version: 1;
  challenge: string; // hex string
  nonce: string; // hex string
  traceContext?: TraceContext;
}

/**
 * Handshake Request Schema (built using Ultra-DRY utilities)
 */
export const HandshakeRequestSchema = buildMessageSchema('handshake', 1, {
  challenge: Uint8ArrayCodec,
  nonce: Uint8ArrayCodec,
  traceContext: TraceContextCodec,
});

export type HandshakeRequest = TypeOf<typeof HandshakeRequestSchema>;

/**
 * Handshake V1 Request Message Class (Ultra-DRY implementation)
 */
export const HandshakeV1RequestMessage = createStandardMessage({
  messageType: 'handshake' as const,
  version: 1 as const,
  schema: HandshakeRequestSchema,
  encodeData: (data: HandshakeRequestData): HandshakeRequestWire =>
    HandshakeRequestSchema.encode({
      type: 'handshake',
      version: 1,
      challenge: data.challenge,
      nonce: data.nonce,
      traceContext: data.traceContext,
    }),
  decodeData: createStandardDecoder<HandshakeRequestData>((decoded) => ({
    challenge: decoded.challenge,
    nonce: decoded.nonce,
    traceContext: decoded.traceContext,
  })),
});
