import { either } from 'fp-ts';
import { Errors, Type } from 'io-ts';
import { BaseMessage, IMessage } from '../base';

/**
 * Configuration for creating a standard message class
 */
export interface StandardMessageConfig<
  TData,
  TWire,
  TType extends string,
  TVersion extends number,
> {
  messageType: TType;
  version: TVersion;
  schema: Type<any>;
  encodeData: (data: TData, type: TType, version: TVersion) => TWire;
  decodeData: (decoded: any) => TData;
}

/**
 * Standard message class factory - eliminates boilerplate
 * Creates a complete message class with encode/decode functionality
 */
export function createStandardMessage<
  TData,
  TWire,
  TType extends string,
  TVersion extends number,
>(config: StandardMessageConfig<TData, TWire, TType, TVersion>) {
  class StandardMessageClass
    extends BaseMessage<TData, TWire>
    implements IMessage<TData, TWire>
  {
    static readonly MESSAGE_TYPE = config.messageType;
    static readonly MESSAGE_VERSION = config.version;
    static readonly schema = config.schema;

    readonly type = config.messageType;
    readonly version = config.version;

    constructor(data: TData) {
      super(data);
    }

    encode(): TWire {
      return config.encodeData(this.data, this.type, this.version);
    }

    static decode(
      wireData: unknown
    ): either.Either<Errors, StandardMessageClass> {
      const result = config.schema.decode(wireData);
      if (either.isLeft(result)) {
        return result;
      }

      const decoded = result.right;
      return {
        _tag: 'Right',
        right: new StandardMessageClass(config.decodeData(decoded)),
      };
    }
  }

  return StandardMessageClass;
}

/**
 * Type helper to extract the message class type from createStandardMessage
 */
export type StandardMessageClass<
  TData,
  TWire,
  TType extends string,
  TVersion extends number,
> = ReturnType<typeof createStandardMessage<TData, TWire, TType, TVersion>>;

/**
 * Simplified message factory for messages with identical runtime and wire formats
 */
export function createSimpleMessage<
  TData,
  TType extends string,
  TVersion extends number,
>(config: { messageType: TType; version: TVersion; schema: Type<any> }) {
  return createStandardMessage({
    messageType: config.messageType,
    version: config.version,
    schema: config.schema,
    encodeData: (data: TData) =>
      ({
        type: config.messageType,
        version: config.version,
        ...data,
      }) as any,
    decodeData: (decoded: any) => {
      const { type, version, ...data } = decoded;
      return data as TData;
    },
  });
}

/**
 * Message factory with field transformations - eliminates encodeData/decodeData duplication
 */
export function createMessageWithTransforms<
  TData,
  TWire = TData & { type: string; version: number },
  TType extends string = string,
  TVersion extends number = number,
>(config: {
  messageType: TType;
  version: TVersion;
  schema: Type<any>;
  transforms?: Record<string, (value: any) => any>;
  normalizers?: Partial<Record<keyof TData, (input: any) => any>>;
}) {
  return createStandardMessage({
    messageType: config.messageType,
    version: config.version,
    schema: config.schema,
    encodeData: (data: TData) => {
      const encoded: any = {
        type: config.messageType,
        version: config.version,
      };

      // Apply field transformations
      const dataObj = data as Record<string, any>;
      for (const [key, value] of Object.entries(dataObj)) {
        if (value !== undefined) {
          const transform = config.transforms?.[key];
          encoded[key] = transform ? transform(value) : value;
        }
      }

      return encoded as TWire;
    },
    decodeData: (decoded: any) => {
      // Apply normalizers to convert wire format to runtime format
      const normalizedData: any = { ...decoded };

      if (config.normalizers) {
        for (const [key, normalizer] of Object.entries(config.normalizers)) {
          if (
            key in normalizedData &&
            normalizer &&
            typeof normalizer === 'function' &&
            normalizedData[key] !== undefined
          ) {
            const normalizedValue = normalizer(normalizedData[key]);
            if (normalizedValue !== undefined) {
              normalizedData[key] = normalizedValue;
            }
          }
        }
      }

      // Remove type and version, return runtime data
      const { type, version, ...data } = normalizedData;
      return data as TData;
    },
  });
}
