import { TypeOf, string } from 'io-ts';
import {
  Uint32ArrayCodec,
  Uint8ArrayCodec,
  type TraceContext,
  TraceContextCodec,
  OptionalStringCodec,
} from '../codecs';
import {
  HashAlgorithm,
  SignatureAlgoSchema,
  fromDynamicSigningAlgorithm,
} from '../constants';
import { DomainCodec } from '../codecs/DomainCodec';
import {
  EncryptedKeyshareCodec,
  type EncryptedKeyshare,
} from '../codecs/EncryptedKeyshareCodec';
import {
  buildMessageSchema,
  createStandardMessage,
  createStandardDecoder,
  createComplexEncoder,
} from './utils';
import { Uint8ArrayOrHexCodec } from '../codecs/Uint8ArrayOrHexCodec';
import { SigningAlgorithm } from '@dynamic-labs-wallet/core';

/**
 * Sign Message Request Runtime Data
 */
export interface SignMessageRequestData {
  relayDomain: string;
  signingAlgo: SigningAlgorithm;
  hashAlgo?: HashAlgorithm;
  derivationPath?: Uint32Array;
  tweak?: Uint8Array;
  keyshare: EncryptedKeyshare;
  message: Uint8Array | string;
  roomUuid: string;
  userId?: string;
  environmentId?: string;
  traceContext?: TraceContext;
}

/**
 * Sign Message Request Wire Format
 */
export interface SignMessageRequestWire {
  type: 'signMessage';
  version: 1;
  relayDomain: string;
  signingAlgo: SigningAlgorithm;
  hashAlgo?: HashAlgorithm;
  derivationPath?: string; // hex string
  tweak?: string; // hex string
  keyshare: {
    salt: string; // hex string
    encryptedPayload: string; // hex string
  };
  message: string; // hex string
  roomUuid: string;
  userId?: string;
  environmentId?: string;
  traceContext?: TraceContext;
}

/**
 * Sign Message Request Schema - uses codecs that handle both formats automatically
 */
export const SignMessageRequestSchema = buildMessageSchema(
  'signMessage',
  1,
  {
    relayDomain: DomainCodec,
    keyshare: EncryptedKeyshareCodec,
    message: Uint8ArrayOrHexCodec,
    roomUuid: string,
    userId: OptionalStringCodec,
    environmentId: OptionalStringCodec,
    traceContext: TraceContextCodec,
  },
  SignatureAlgoSchema
);

export type SignMessageRequest = TypeOf<typeof SignMessageRequestSchema>;

/**
 * Sign Message V1 Request Message Class - Clean implementation using createComplexEncoder
 */
export const SignMessageV1RequestMessage = createStandardMessage({
  messageType: 'signMessage' as const,
  version: 1 as const,
  schema: SignMessageRequestSchema,
  encodeData: createComplexEncoder('signMessage', 1, {
    signingAlgo: (value: SigningAlgorithm) =>
      fromDynamicSigningAlgorithm(value),
    derivationPath: (value: Uint32Array) => Uint32ArrayCodec.encode(value),
    tweak: (value: Uint8Array) => Uint8ArrayCodec.encode(value),
    keyshare: (value: EncryptedKeyshare) =>
      EncryptedKeyshareCodec.encode(value),
    message: (value: Uint8Array | string) => Uint8ArrayOrHexCodec.encode(value),
  }),
  decodeData: createStandardDecoder<SignMessageRequestData>((decoded) => ({
    relayDomain: decoded.relayDomain,
    signingAlgo: decoded.signingAlgo,
    hashAlgo: decoded.hashAlgo,
    derivationPath: decoded.derivationPath,
    tweak: decoded.tweak,
    keyshare: decoded.keyshare,
    message: decoded.message,
    roomUuid: decoded.roomUuid,
    userId: decoded.userId,
    environmentId: decoded.environmentId,
    traceContext: decoded.traceContext,
  })),
});
