/**
 * Base interface for all messages
 */
export interface IMessage<TRuntime = any, TWire = any> {
  readonly type: string;
  readonly version: number;

  /**
   * Encode this message to wire format for transmission
   */
  encode(): TWire;

  /**
   * Get the runtime data of this message
   */
  getData(): TRuntime;
}

/**
 * Abstract base class for all messages
 */
export abstract class BaseMessage<TRuntime = any, TWire = any>
  implements IMessage<TRuntime, TWire>
{
  abstract readonly type: string;
  abstract readonly version: number;

  protected constructor(public readonly data: TRuntime) {}

  abstract encode(): TWire;

  getData(): TRuntime {
    return this.data;
  }
}
