import { intersection, literal, Mixed, type, Type } from 'io-ts';

/**
 * Build standard message schema with type and version
 * Eliminates duplication across all message schemas
 */
export function buildMessageSchema<T extends string, V extends number>(
  messageType: T,
  version: V,
  additionalFields: Record<string, any> = {},
  ...additionalSchemas: Mixed[]
): Type<any> {
  const schema = type({
    type: literal(messageType),
    version: literal(version),
    ...additionalFields,
  });

  if (additionalSchemas.length === 0) {
    return schema;
  }

  if (additionalSchemas.length === 1) {
    return intersection([schema, additionalSchemas[0]]);
  }

  // For multiple schemas, chain intersections
  let result: Type<any> = intersection([schema, additionalSchemas[0]]);
  for (let i = 1; i < additionalSchemas.length; i++) {
    result = intersection([result, additionalSchemas[i]]);
  }
  return result;
}

/**
 * Message schema configuration interface
 */
export interface MessageSchemaConfig<T extends string, V extends number> {
  type: T;
  version: V;
  fields: Record<string, any>;
}

/**
 * Create schema from configuration
 */
export function createSchemaFromConfig<T extends string, V extends number>(
  config: MessageSchemaConfig<T, V>
): Type<any> {
  return buildMessageSchema(config.type, config.version, config.fields);
}
