import {
  partial,
  string,
  number,
  union,
  undefined as undefinedType,
} from 'io-ts';

/**
 * Trace Context for request tracing
 */
export interface TraceContext {
  traceId?: string;
  startTime?: number;
}

/**
 * Trace Context Schema - validates trace context with optional fields
 */
const TraceContextSchema = partial({
  traceId: string,
  startTime: number,
});

/**
 * Trace Context Codec - optional by default (allows undefined)
 */
export const TraceContextCodec = union([TraceContextSchema, undefinedType]);

