import { either } from 'fp-ts';
import { IMessage } from './base';
import { getMessageClass, getAllSupportedMessages } from './allMessages';

/**
 * Ultra-DRY Message Registry - everything is derived, no registration needed
 */
export class MessageRegistry {
  private static instance: MessageRegistry;

  private constructor() {}

  static getInstance(): MessageRegistry {
    if (!MessageRegistry.instance) {
      MessageRegistry.instance = new MessageRegistry();
    }
    return MessageRegistry.instance;
  }

  /**
   * Get a message class by type and version (derived from single source)
   */
  getMessageClass(type: string, version: number) {
    try {
      return getMessageClass(type, version);
    } catch {
      return undefined;
    }
  }

  /**
   * Create a message instance from wire data (using derived message classes)
   */
  decode(wireData: any): either.Either<string, IMessage> {
    if (!wireData || typeof wireData !== 'object') {
      return { _tag: 'Left', left: 'Invalid wire data: must be an object' };
    }

    const { type, version } = wireData;
    if (!type || !version) {
      return {
        _tag: 'Left',
        left: 'Invalid wire data: missing type or version',
      };
    }

    const MessageClass = this.getMessageClass(type, version);
    if (!MessageClass) {
      return { _tag: 'Left', left: `Unknown message type: ${type}@${version}` };
    }

    const result = MessageClass.decode(wireData);
    if (either.isLeft(result)) {
      return {
        _tag: 'Left',
        left: `Decode error: ${result.left.map((e) => e.message).join(', ')}`,
      };
    }

    return { _tag: 'Right', right: result.right };
  }

  /**
   * Encode a message to wire format
   */
  encode(message: IMessage): any {
    return message.encode();
  }

  /**
   * Get all registered message types (derived from single source)
   */
  getRegisteredTypes(): string[] {
    return getAllSupportedMessages().map(
      ({ type, version }) => `${type}@${version}`
    );
  }
}

// Export singleton instance
export const messageRegistry = MessageRegistry.getInstance();
