/**
 * Standard encoder for simple messages
 * Creates basic encode function with type and version
 */
export function createSimpleEncoder<TData, TWire>(
  messageType: string,
  version: number
) {
  return (data: TData): TWire =>
    ({
      type: messageType,
      version,
      ...data,
    }) as TWire;
}

/**
 * Standard encoder for complex messages with codec transformations
 * Handles field transformations like Uint8Array -> hex string
 */
export function createComplexEncoder<TData, TWire>(
  messageType: string,
  version: number,
  fieldTransforms: Record<string, (value: any) => any> = {}
) {
  return (data: TData): TWire => {
    const encoded: any = {
      type: messageType,
      version,
    };

    // Apply field transformations
    for (const [key, value] of Object.entries(data as any)) {
      if (value !== undefined) {
        encoded[key] = fieldTransforms[key]
          ? fieldTransforms[key](value)
          : value;
      }
    }

    return encoded as TWire;
  };
}

/**
 * Standard decoder with field extraction
 * Extracts runtime data from decoded wire format
 */
export function createStandardDecoder<TData>(
  fieldExtractor: (decoded: any) => TData
) {
  return (decoded: any): TData => fieldExtractor(decoded);
}

/**
 * Identity decoder for simple messages where wire format matches runtime format
 */
export function createIdentityDecoder<TData>(): (decoded: any) => TData {
  return (decoded: any): TData => {
    // Remove type and version fields, keep the rest
    const { type, version, ...data } = decoded;
    return data as TData;
  };
}
